.lee-button{background:#fff;border:1px solid #ccc;padding:10px 15px;font-size:14px;cursor:pointer;border-radius:3px;color:#fff;-webkit-transition:.3s;transition:.3s}

.lee-button.lee-button-default{background-color:#fff;border:1px solid #dcdfe6;color:#606266}

.lee-button.lee-button-default.is-disabled{opacity:.3}

.lee-button.lee-button-default.is-disabled.is-plain:hover{background-color:#fff;color:#606266;border-color:#dcdfe6}

.lee-button.lee-button-default.is-plain:hover{background:#fff;color:#409eff}

.lee-button.lee-button-default:hover{background-color:rgba(64,158,255,.09999999999999998);border-color:rgba(64,158,255,.8);color:#409eff}

.lee-button.lee-button-default.is-disabled:hover{background-color:#fff;color:#606266;border-color:#dcdfe6}

