.lee-button.lee-button-primary.is-disabled{opacity:.3}

.lee-button.lee-button-primary.is-disabled.is-plain:hover,.lee-button.lee-button-primary.is-plain{background-color:rgba(64,158,255,.09999999999999998);color:#409eff;border-color:rgba(64,158,255,.4)}

.lee-button.lee-button-primary.is-plain:hover{background:#409eff;color:#fff}

.lee-button.lee-button-primary.is-plain:active,.lee-button.lee-button-primary:hover{background-color:rgba(64,158,255,.8)}

.lee-button.lee-button-primary:hover{border-color:rgba(64,158,255,.8)}

.lee-button.lee-button-primary:active{background-color:#409eff;border-color:#409eff}

.lee-button.lee-button-primary.is-disabled:hover{background-color:#409eff}

