.lee-button.lee-button-success.is-plain:hover{background:#67c23a;color:#fff}

.lee-button.lee-button-success.is-plain:active,.lee-button.lee-button-success:hover{background-color:rgba(103,194,58,.8)}

.lee-button.lee-button-success:hover{border-color:rgba(103,194,58,.8)}

.lee-button.lee-button-success:active{background-color:#67c23a;border-color:#67c23a}

.lee-button.lee-button-success.is-disabled:hover{background-color:#67c23a}

