.lee-button.lee-button-info.is-plain:hover{background:#909399;color:#fff}

.lee-button.lee-button-info.is-plain:active,.lee-button.lee-button-info:hover{background-color:hsla(220,4%,58%,.8)}

.lee-button.lee-button-info:hover{border-color:hsla(220,4%,58%,.8)}

.lee-button.lee-button-info:active{background-color:#909399;border-color:#909399}

