.lee-button.lee-button-size-medium{padding:8px 12px}

.lee-button.lee-button-size-small{padding:5px 10px}

.lee-button.is-disabled{cursor:not-allowed}

.lee-button-group{font-size:0;border-radius:5px;overflow:hidden;display:inline-block}

.lee-button-group .lee-button{border-radius:0;border-top-width:0;border-bottom-width:0;border-left:1px solid #fff;position:relative;margin-left:-1px}

.lee-button-group .lee-button:hover{border-left-color:#fff}

.lee-button-group .lee-button.lee-button-default{border-top-width:1px;border-bottom-width:1px;border-left:1px solid #dcdfe6}

.lee-button-group .lee-button.lee-button-default:hover{border-left-color:#409eff;z-index:1}

.lee-button-group .lee-button.lee-button-default:last-child{border-radius:0 5px 5px 0}

.lee-button-group .lee-button.lee-button-default:first-child{border-radius:5px 0 0 5px;margin-left:0}